.work{
    background-color:lightseagreen;
    display: flex;
    align-content: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 50px;
        position: absolute;

        &.left{
            left: 100px;
            top: 200px;
            transform: rotateY(180deg);
            cursor: pointer;
        }
        &.right{
            right:100px;
            top: 200px;
            cursor: pointer;

        }
    }


    .slider{
        height: 400px;
        display: flex;
        position: absolute;
        left: 0;


        .container {
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 100px;
      
            .item {
              width: 600px;
              height: 100%;
              background-color: white;
              border-radius: 20px;
              display: flex;
              align-items: center;
              justify-content: center;
            }

            .left{
                flex: 4;
                height: 80%;
                display: flex;
                align-content: center;
                justify-content: center;
                

                .leftContainer{
                    width: 90%;
                    height: 70%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    .imgContainer{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        background-color:rgb(111, 187, 124) ;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        img{
                            width: 25px;
                        }
                    }
                
                    h2{
                        font-size: 20px;
                    }
                    p{
                        font-size: 13px;
                    }
                    span{
                        font-size: 12px;
                        font-weight: 600;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                
                
                }
                
            }
            .right{
                flex: 8;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;
            }

            img{
                width: 400px;
                transform: rotate(-10deg);
            }
        }
    }
    
}